class Hubcounter extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            data: [],
            mounted: false
        }

        this.update()
    }

    update() {
        var link = 'https://api.github.com/users/HackerTon/repos'


        fetch(link).then(res => res.json())
            .then(json => {
                this.setState({ data: json, mounted: !this.mounted })
            })
            .catch(err => console.error(this.props.url))
    }

    render() {
        if (this.state.mounted) {
            return (
                <div className='container'>
                    <table className='table table-hover'>
                        <thead class='thead-dark'>
                            <tr>
                                <th scope='col'>Name</th>
                                <th scope='col'>Username</th>
                                <th scope='col'>Description</th>
                                <th scope='col'>Url</th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.data.map(data => {
                                return (
                                    <tr>
                                        <th scope='row'>{data.name}</th>
                                        <td>{data.owner.login}</td>
                                        <td>{data.description}</td>
                                        <td><a href={data.html_url}>{data.html_url}</a></td>
                                    </tr>
                                )
                            })}
                        </tbody>
                    </table>
                </div>
            )
        } else {
            return (
                <div className='container'>
                </div>
            )
        }

    }

}

class Button extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            passTrue: true,
        };

        this.onclick = this.onclick.bind(this)

    }

    onclick() {
        this.setState(state => ({
            passTrue: !state.passTrue
        }));
    }

    render() {
        console.log('debug')

        return (
            <div>
                <button onClick={this.onclick}>{this.state.passTrue ? 'You Found mE' : 'Oi!'}</button>

                {this.state.passTrue ? '' : 'Press! Press! Press! You kid ahhh?!'}
            </div>
        )
    }
}

